import React, { Component, Fragment } from 'react';
import { List, ListItem } from 'material-ui/List';
import Divider from 'material-ui/Divider';
import Subheader from 'material-ui/Subheader';
import Avatar from 'material-ui/Avatar';
import { grey400, darkBlack, lightBlack } from 'material-ui/styles/colors';
import Drawer from 'material-ui/Drawer';
import FullscreenDialog from 'material-ui-fullscreen-dialog'
import IconButton from 'material-ui/IconButton';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import AppBar from 'material-ui/AppBar';
import { Card, CardTitle, CardText } from 'material-ui/Card';
import Auth from '../models/auth';
import Gdrive from '../models/gdrive';
import Dialog from 'material-ui/Dialog';
import { RadioButton, RadioButtonGroup } from 'material-ui/RadioButton';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';
import axios from 'axios';
import Player from './Player';

/**
 * Индексирует массив
 *
 * @param array
 * @param property
 * @return {{}}
 */
const indexArray = (array, property) => {
  let object = {};
  if (Array.isArray(array) && array.length > 0) {
    array.map(item => {
      if (item) {
        object[item[property]] = item;
      }
    });
  }
  return object;
};

// const rightIconMenu = id => (
  
// );

class EpisodesList extends Component {

  state = {
    drawerOpen: false,
    selectFolderDialogOpen: false,
    folderList: [],
    items: [],
    itemToShow: null
  }

  gdrive = null;

  constructor(props) {
    super(props);
    this.gdrive = new Gdrive(Auth.gapi);
  }

  componentDidMount() {

    this.gdrive.loadFolder().then(folder => {
      if (folder === null) {
        this.gdrive.getFoldersList().then(list => {
          this.setState({
            selectFolderDialogOpen: true,
            folderList: list,
          });
        });
      } else {
        this.loadMetaData(folder);
      }
    });
  }

  toggleDrawer = (itemToShow) => {
    this.setState({
      drawerOpen: !this.state.drawerOpen,
      itemToShow: itemToShow
    });
  }

  handleCloseSelectFolderDialog = (forceClose) => {
    if (!this.state.selectedFolderId && forceClose !== true) {
      alert('Select folder');
      return;
    }

    if (this.state.selectedFolderId) {
      this.gdrive.loadFolder(this.state.selectedFolderId).then(folder => {
        if (folder === null) {
          alert('Wrong folder')
        } else {
          this.gdrive.saveRootFolderId(this.state.selectedFolderId);
          this.setState({
            selectFolderDialogOpen: false
          });
          this.loadMetaData(folder);
        }
      });
    } else {
      this.setState({
        selectFolderDialogOpen: false
      });
    }
  }

  handleSelectFolder = (folderId) => {
    this.setState({
      selectedFolderId: folderId
    });
  }

  loadMetaData(folder) {
    // TODO: refactor
    let metadataFileId = folder['metadata.json'].id,
      mp3FolderId = folder['mp3'].id,
      coversFolderId = folder['img'].id;

    this.gdrive.getFileContent(metadataFileId).then(metadata => {
      metadata = indexArray(metadata.fshow, 'id');
      this.gdrive.getFolderContent(mp3FolderId).then(mp3 => {        
        mp3 = indexArray(mp3, 'name');
        this.gdrive.getFolderContent(coversFolderId).then(covers => {
          
          covers = indexArray(covers, 'name');
              
          let items = [];

          Object.keys(metadata).forEach(id => {
            items.push({
              ...metadata[id],
              mp3: mp3[id + '.mp3'] ? mp3[id + '.mp3'].webContentLink : '',
              cover: covers[id + '.jpg'] ? covers[id + '.jpg'].webContentLink : '',
            });
          });        

          this.setState({
            items: items
          });
        });
      });      
    });
  }

  play(item) {
    let playerEl = document.getElementById('player');
    playerEl.pause();
    playerEl.currentTime = 0;
    this.setState({
      playedItem: item,
      autoplay: true
    }, () => {
      playerEl.load();
      playerEl.play();
    });
    // TODO: dispatch play item
  }

  render() {

    const selectFolderDialogActions = [
      <FlatButton
        label="Отмена"
        primary={false}
        onClick={() => { this.handleCloseSelectFolderDialog(true) }}
      />,
      <FlatButton
        label="Выбрать"
        primary={true}
        onClick={this.handleCloseSelectFolderDialog}
      />,
    ];

    // TODO: split by components

    return (
      <Fragment>
        <List className="EpisodesList">
          <Subheader>Все роли</Subheader>
          {this.state.items.map((item, key) => {
            return (
              <Fragment key={key}>
                <ListItem
                  leftAvatar={item.cover ? <Avatar src={item.cover} /> : null}
                  primaryText={item.title}
                  secondaryText={item.bio}
                  secondaryTextLines={1}
                  rightIconButton={<button onClick={event => {this.play(item)}}>Play</button>}
                  onClick={() => this.toggleDrawer(item)}
                />
                <Divider />
              </Fragment>
            );
          })}
        </List>
        <Player src={this.state.playedItem ? this.state.playedItem.mp3 : null} autoplay={this.state.autoplay} />
        <FullscreenDialog
          title={this.state.itemToShow && this.state.itemToShow.title}
          open={this.state.drawerOpen}
          onRequestClose={this.toggleDrawer}
        >
          <Card>
            <CardText>
              {this.state.itemToShow && this.state.itemToShow.cover && <center><img src={this.state.itemToShow.cover} /></center>}
              <p>{this.state.itemToShow && this.state.itemToShow.bio}</p>
            </CardText>
          </Card>
        </FullscreenDialog>
        <Dialog
          title="Выберите папку"
          actions={selectFolderDialogActions}
          modal={true}
          open={this.state.selectFolderDialogOpen}
          onRequestClose={() => { this.handleCloseSelectFolderDialog(true) }}
          autoScrollBodyContent={true}
        >
          <RadioButtonGroup name="folders" defaultSelected="">
            {this.state.folderList.map((folder, key) => {
              return (
                <RadioButton
                  key={key}
                  value={folder.id}
                  label={folder.name}
                  onClick={() => this.handleSelectFolder(folder.id)}
                />
              )
            })}
          </RadioButtonGroup>
        </Dialog>
      </Fragment>
    );
  }
}

export default EpisodesList;


