
const KEY_FSHOW_ROOT_FOLDER_ID = 'FSHOW_ROOT_FOLDER_ID';

class Gdrive {

  folderId = null;
  gapi = null;

  constructor(gapi) {
    this.gapi = gapi;
    this.folderId = localStorage.getItem(KEY_FSHOW_ROOT_FOLDER_ID);
  }

  loadFolder(folderId) {
    if (folderId === undefined) {
      folderId = this.folderId;
    }

    return new Promise(resolve => {
      if (!folderId) {
        resolve(null);
        return;
      }

      this.gapi.client.drive.files.list({
        'q': `'${folderId}' in parents`,
        'pageSize': 5,
        'fields': "nextPageToken, files(id, name, webContentLink)"
      }).then(response => {
        let foundFilesAndFolders = {
          'metadata.json': false,
          'mp3': false,
          'img': false,
          'img-th': false
        }
        var files = response.result.files;
        if (files && files.length > 0) {
          for (var i = 0; i < files.length; i++) {
            let file = files[i];
            foundFilesAndFolders[file.name] = file;
          }
        }
        let isCorrectFolder = Object.values(foundFilesAndFolders).every(value => value !== false);
        resolve(isCorrectFolder ? foundFilesAndFolders : null);
      });
    });
  }

  getFoldersList() {
    return new Promise(resolve => {
      this.gapi.client.drive.files.list({
        'q': 'sharedWithMe or \'root\' in parents',
        'pageSize': 1000
      }).then(response => {
        resolve(response.result.files.filter(item => item.mimeType === 'application/vnd.google-apps.folder'));
      });
    });
  }

  getFolderContent(folderId) {
    return new Promise(resolve => {
      if (!folderId) {
        resolve(null);
        return;
      }

      this.gapi.client.drive.files.list({
        'q': `'${folderId}' in parents`,
        'pageSize': 1000,
        'fields': "nextPageToken, files(id, name, webContentLink)"
      }).then(response => {
        resolve(response.result.files);
      });
    });
  }

  saveRootFolderId(folderId) {
    localStorage.setItem(KEY_FSHOW_ROOT_FOLDER_ID, folderId);
    // TODO: save data.json, direct links to mp3 and images
  }

  getFileContent(fileId) {
    return new Promise(resolve => {
      this.gapi.client.drive.files.export({
        'fileId': fileId,
        'mimeType': 'text/plain',
      }).then(success => {
        let content = success.status === 200 && success.body 
          ? JSON.parse(success.body.trim())
          : null;      
        resolve(content);
      }, fail => {
        //console.log('Error ', fail);
        resolve(null);
      });
    });    
  }


}

export default Gdrive;


