import React, { Component, Fragment } from 'react';
import './App.css';
import lightBaseTheme from 'material-ui/styles/baseThemes/lightBaseTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import EpisodesList from './components/EpisodesList';
import Player from './components/Player';
import Auth from './models/auth';

class App extends Component {

  state = {
    open: false,
    auth: Auth
  }

  constructor(props) {
    super(props);
    this.state.auth.initClient(this);
  }

  toggleDrawer = () => this.setState({ open: !this.state.open });

  render() {
    return (
      <div className="App">
        <MuiThemeProvider muiTheme={getMuiTheme(lightBaseTheme)}>
          <Fragment>
            <AppBar
              title="Закрой глаза и смотри"
              className="AppBar"
              style={{ position: 'fixed', top: 0 }}
              onLeftIconButtonClick={this.toggleDrawer}
            />
            <Drawer width={200} open={this.state.open}>
              <AppBar
                title=""
                className="AppBar"
                style={{ position: 'fixed', top: 0 }}
                iconElementLeft={<IconButton><NavigationClose /></IconButton>}
                onLeftIconButtonClick={this.toggleDrawer}
              />
              <MenuItem>Menu Item</MenuItem>
              <MenuItem>Menu Item 2</MenuItem>
            </Drawer>

            {this.state.auth.isReady && (
              <Fragment>
                {this.state.auth.isAuth
                  ? (
                    <Fragment>
                      <EpisodesList />                      
                    </Fragment>
                  )
                  : (
                    <center style={{ marginTop: '100px' }}>
                      <button onClick={() => { this.state.auth.signIn() }}>Login with Google</button>
                    </center>
                  )
                }
              </Fragment>
            )}
          </Fragment>
        </MuiThemeProvider>
      </div>
    );
  }
}

export default App;



