import config from '../config';

class Auth {

  static component = null;

  static isReady = false;

  static isAuth = false;

  static gapi = null;

  static initClient(component) {
    if (Auth.isAuth || !window.hasOwnProperty('gapi')) {
      setTimeout(() => {
        Auth.initClient(component);
      }, 500);
      return;      
    }

    Auth.component = component;

    Auth.gapi = window.gapi;

    Auth.gapi.load('client:auth2', () => {
      Auth.gapi.client.init({
        apiKey: config.GDRIVE_API_KEY,
        clientId: config.GDRIVE_CLIENT_ID,
        discoveryDocs: config.GDRIVE_DISCOVERY_DOCS,
        scope: config.GDRIVE_SCOPES
      }).then(() => {
        Auth.isReady = true;

        // Listen for sign-in state changes.
        Auth.gapi.auth2.getAuthInstance().isSignedIn.listen(Auth.updateSigninStatus);

        // Handle the initial sign-in state.
        Auth.updateSigninStatus(Auth.gapi.auth2.getAuthInstance().isSignedIn.get());
        // authorizeButton.onclick = handleAuthClick;
        // signoutButton.onclick = handleSignoutClick;
        // listFolderButton.onclick = handleListFolderClick;        
      });
    });
  }

  static updateSigninStatus(isSignedIn) {
    Auth.isAuth = isSignedIn;
    // dispatch action/props (type: AUTH_STATUS, payload: {isAuth: true})
    Auth.component.forceUpdate();
  }

  // static isAuth() {

  // }

  static getAccessToken() {
    return Auth.gapi.auth.getToken().access_token
    // let user = Auth.gapi.auth2.getAuthInstance().currentUser.get();
    // return user.getAuthResponse().access_token;
  }

  static signIn() {
    Auth.gapi.auth2.getAuthInstance().signIn();
  }

  static signOut() {
    Auth.gapi.auth2.getAuthInstance().signOut();
    Auth.isAuth = false;
  }
}

export default Auth;



